/*************************************************************************************************/
/*************************************************************************************************/
.main-logo {
  max-width: 100vw; }

.filter-portfolio {
  text-align: center; }

.filter-item {
  display: inline-block;
  margin: 0.5rem;
  vertical-align: top;
  position: relative;
  min-width: 180px; }

.filter-item .filter-card-parent {
  border: 1px solid #ffeaa7;
  border-radius: 16px;
  text-align: left;
  position: relative;
  padding: 0.5rem;
  padding-left: 2.0rem;
  background-color: #ffeaa7; }

.filter-item .filter-card-parent input {
  top: 50%;
  left: 3%;
  position: absolute;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%); }

.filter-item .card-body {
  padding: 0.3rem;
  text-align: center;
  width: 75%;
  right: 5%;
  top: 5%;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border: 1px solid #fff;
  background-color: #fff;
  height: 62px;
  width: 165px; }

.filter-item .card-body img {
  max-width: 50px;
  max-height: 50px;
  margin-top: 0.1rem;
  position: absolute;
  left: 36px;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%); }

.filter-item .filter-category {
  font-size: 0.8rem;
  height: 1.1rem;
  position: absolute;
  left: 54%;
  -webkit-transform: translateX(-30%);
      -ms-transform: translateX(-30%);
          transform: translateX(-30%);
  top: 30%; }

.filter-item .filter-count {
  position: absolute;
  right: 18px;
  top: 10px;
  font-size: 1.8rem; }

/*************************************************************************************************/
/*************************************************************************************************/
.portfolio {
  vertical-align: top;
  text-align: center; }

.portfolio-card {
  display: inline-block;
  vertical-align: top;
  max-width: 350px;
  margin: 0.5rem; }

.portfolio-card .card-top-half {
  min-height: 301px; }

.portfolio-card .img-container {
  text-align: center;
  min-height: 150px;
  vertical-align: top; }

.portfolio-card .card-img-top {
  width: auto;
  max-width: 80%;
  max-height: 150px;
  vertical-align: middle; }

.portfolio-card .technology-list .technology-chip {
  margin-top: 0.5rem;
  display: inline-block; }

.portfolio-card .technology-list .technology-chip img {
  max-width: 50px;
  margin: 0.3rem 0.2rem; }

.portfolio-card .list-group-item {
  padding: 0; }

.portfolio-card .important-links {
  position: relative;
  min-height: 50px; }

.portfolio-card .see-live {
  margin-right: 2rem; }

.portfolio-card .github-repo {
  max-width: 60px; }

.portfolio-card.react-playground .card-img-top, .portfolio-card.redux-counter .card-img-top, .portfolio-card.react-todo-1 .card-img-top,
.portfolio-card.react-redux-todo .card-img-top {
  width: 80%;
  padding-top: 1.5rem; }

.portfolio-card.react-playground .card-img-top {
  padding-top: 1rem; }

.portfolio-card.react-redux-todo .card-img-top {
  padding-top: 2rem; }

@media screen and (min-width: 650px) {
  .portfolio-card {
    width: 45%; } }

@media screen and (min-width: 900px) {
  .portfolio-card {
    width: 31%;
    max-width: 325px; } }



